.container {
	position: relative;
	width: 94%;
	height: 100%;
	margin-top: 1rem;
	margin-bottom: 1rem;
	z-index: 100;
}

.message {
	display: flex;
	padding: 0.4rem;
	/* margin-bottom: 0.2rem; */
	/* background-color: #ccc; */
}

.ava {
	width: 2.5rem;
	height: 2.5rem;
	background-color: red;
	border-radius: 0.2rem;
	margin-right: 0.4rem;
	position: absolute;
}

.body {
	display: flex;
	margin-left: 2.8rem;
	width: calc(100% - 3.6rem);
	height: 1.7rem;
	padding: 0.4rem;
	border-radius: 0.2rem;
}

.nickname {
	height: 0.7rem;
}

.text {
	margin-left: 0.4rem;
	height: 0.7rem;
	background-color: rgba(255, 255, 255, 0.1);
}
