.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 320px;
	height: 578px;
	background-color: #000000;
	border-radius: 0.5rem;
	overflow: hidden;
	z-index: 2;
	/* opacity: 0.4; */
	/* transform: skewY(-5deg); */
}

.counter {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 100%;
	height: 60%;
	background: linear-gradient(0.25turn, rgba(148, 127, 238), rgb(244, 93, 161));
	border-bottom-right-radius: 0.7rem;
	border-bottom-left-radius: 0.7rem;
}

.hearts {
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: center;
}

.count {
	color: #ffffff;
	font-weight: 200;
	font-size: 2.2rem;
}

.heart {
	width: 2.2rem;
	height: 2rem;
	margin-right: 0.2rem;
}

.table {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 4rem;
}

.bottle {
	position: absolute;
	height: 4rem;
	pointer-events: none;
}

.tableNumber {
	background-color: rgba(0, 0, 0, 0.3);
	padding: 0.2rem 0.5rem;
	color: #ffffff;
	z-index: 1;
	font-weight: 100;
	font-size: 1.2rem;
}

@media (prefers-reduced-motion: no-preference) {
	.bottle {
		animation: bottle-spin infinite 2s linear;
	}
}

@keyframes bottle-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
