@font-face {
	font-family: 'Ubuntu';
	src: local('Ubuntu'), url(./fonts/Ubuntu-Light.ttf) format('woff');
	font-weight: 100;
}

@font-face {
	font-family: 'Ubuntu';
	src: local('Ubuntu'), url(./fonts/Ubuntu-Regular.ttf) format('woff');
	font-weight: 200;
}

@font-face {
	font-family: 'Ubuntu';
	src: local('Ubuntu'), url(./fonts/Ubuntu-Medium.ttf) format('woff');
	font-weight: 300;
}

@font-face {
	font-family: 'Ubuntu';
	src: local('Ubuntu'), url(./fonts/Ubuntu-Bold.ttf) format('woff');
	font-weight: 400;
}

body {
	/* background-color: #08061b; */
	margin: 0;
	font-family: 'Ubuntu', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
		'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
