.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
}

.appLink {
	padding: 0.9rem 0;
	width: 94%;
	text-align: center;
	background: linear-gradient(0.25turn, rgba(148, 127, 238, 0.2), rgba(244, 93, 162, 0.11));
	font-size: 0.8rem;
	border-radius: 0.4rem;
	color: #e65bff;
	text-decoration: none;
	font-weight: 300;
	transition: 0.5s;
}

.appLink:hover {
	background: rgba(148, 127, 238, 0.14);
}

.authorLink {
	margin-top: 0.4rem;
	font-size: 0.78rem;
	color: white;
	font-weight: 100;
	opacity: 0.9;
}

.authorLink > a {
	color: #e65bff;
	text-decoration: none;
}

.authorLink > a:hover {
	text-decoration: underline;
}
